import { useState, useEffect } from 'react';

import { getGifs } from './../helpers/getGifs'; 



export const useFetchGif = ( category ) =>{


	console.log("fetch");

	const [state, setState] = useState({
		data: [],
		loading: true
	});



	useEffect(()=>{

		getGifs(category).then(imgs => {

			setState({

				data: imgs,

				loading: false

			});	

		});

	}, [category]);

	return state;
}